console.clear();

import './style.css';
import * as THREE from  'three'
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/src/ScrollTrigger.js';
import {GLTFLoader} from 'three/examples/jsm/loaders/GLTFLoader.js';

gsap.registerPlugin(ScrollTrigger);

// --- CONSTS

const COLORS = {
	background: '#87CEEB',
	light: '#F0FFFF',
	sky: '#191970',
	ground: '#000000',
    blue:"steelblue"
}

const PI = Math.PI;

const wireFrameMaterial = new THREE.MeshBasicMaterial({color:'white', wireframe:true})

// --- SCENE

const scenes = {
    real: new THREE.Scene(),
    wire: new THREE.Scene()
};
scenes.wire.overrideMaterial= wireFrameMaterial

let size = { width: 0, height: 0 }
scenes.real.background = new THREE.Color(COLORS.background);
scenes.real.fog = new THREE.Fog(COLORS.background, 15, 20);
scenes.wire.background = new THREE.Color(COLORS.blue)

const views = [
    {height:1, bottom:0, scene:scenes.real, camera:null},
    {height:0, bottom:0, scene:scenes.wire, camera:null},
];

// --- RENDERER

const renderer = new THREE.WebGLRenderer({
  antialias: true
})

renderer.physicallyCorrectLights = true;
renderer.outputEncoding = THREE.sRGBEncoding;
renderer.toneMapping = THREE.ReinhardToneMapping;
renderer.toneMappingExposure = 5;
renderer.shadowMap.enabled = true;

const container = document.querySelector('.canvas-container');
container.appendChild( renderer.domElement );

// --- CAMERA

let cameraTarget = new THREE.Vector3(0, 1, 0);

views.forEach(view => {

view.camera = new THREE.PerspectiveCamera(40, size.width / size.height, 0.1, 5000);
view.camera.position.set(0, 1, 5);
view.scene.add(view.camera);
})


// --- LIGHTS

const directionalLight = new THREE.DirectionalLight(COLORS.light, 2);
directionalLight.castShadow = true;
directionalLight.position.set(2, 5, 3);

scenes.real.add(directionalLight);

const hemisphereLight = new THREE.HemisphereLight( COLORS.sky, COLORS.ground, 0.5 );
scenes.real.add(hemisphereLight)

// --- FLOOR

const plane = new THREE.PlaneGeometry(100, 100);
const floorMaterial = new THREE.MeshStandardMaterial({ color: COLORS.ground })
const floor = new THREE.Mesh(plane, floorMaterial);
floor.receiveShadow = true;
floor.rotateX(-Math.PI * 0.5)

scenes.real.add(floor);

// --- ON RESIZE

const onResize = () => {
	size.width = container.clientWidth;
	size.height = container.clientHeight;

	views.forEach(view => {
        view.camera.aspect = size.width / size.height;
	    view.camera.updateProjectionMatrix();
    })

	renderer.setSize(size.width, size.height);
	renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
}

window.addEventListener('resize', onResize)
onResize();

// --- TICK

const tick = () => {
    views.forEach(view =>{
        view.camera.lookAt(cameraTarget);
        let bottom = size.height * view.bottom
        let height = size.height * view.height
        renderer.setViewport(0,0, size.width, size.height)
        renderer.setScissor(0, bottom, size.width, height)
        renderer.setScissorTest(true)
        renderer.render(view.scene, view.camera);
    })
    window.requestAnimationFrame(() => tick());
};

tick();

const toLoad = [
    {
        name:"normalCar",
        group:new THREE.Group(),
        file: "/models/NormalCar/normalCar.glb"
    },
    {   name:"RaceCar",
        group:new THREE.Group(),
        file:"/models/RaceCar/RaceCar.glb"
    },
    {   name:"street",
        group: new THREE.Group(),
        file:"/models/street/street.glb"
    },
    {   name:"clouds",
    group: new THREE.Group(),
    file:"/models/street/clouds.glb"
    },
    {   name:"StreetSign",
    group: new THREE.Group(),
    file:"/models/street/Street_sign.glb"
    }
    
];

const Models = {};
const clones = {}
let cameras = null
let normalCars = null
let raceCars = null


const setupAnimation = () => {
        cameras = {position: [views[0].camera.position, views[1].camera.position]}
        normalCars = {position: [Models.normalCar.position, clones.normalCar.position],rotation: [Models.normalCar.rotation, clones.normalCar.rotation]}
        raceCars = {position: [Models.RaceCar.position, clones.RaceCar.position],rotation: [Models.RaceCar.rotation, clones.RaceCar.rotation]}
        //street//
        Models.street.position.x = 5
        Models.street.position.y = 0.15
        Models.street.rotation.y = -1.6
        Models.StreetSign.rotation.y = 4
        Models.StreetSign.position.z = -7
        Models.StreetSign.position.x = 6
        Models.street.receiveShadow = true
        //Clouds
        Models.clouds.rotation.y = 2
        Models.clouds.position.y = 2.5
        //cars
        Models.normalCar.position.z = -3
        Models.normalCar.position.x = 7
        clones.normalCar.position.z = -3
        clones.normalCar.position.x = 7
        Models.normalCar.rotation.y = 2
        clones.normalCar.rotation.y = 2
        Models.RaceCar.position.x = -6
        clones.RaceCar.position.x = -6
        Models.RaceCar.rotation.y = 4.7
        clones.RaceCar.rotation.y = 4.7
        Models.RaceCar.receiveShadow = true
        Models.RaceCar.castShadow = true
        clones.RaceCar.receiveShadow = true
        clones.RaceCar.castShadow = true
    
        ScrollTrigger.matchMedia({"(prefers-reduced-motion: no-preference)":desktopAnimation})
}
const desktopAnimation = () =>{
   let section = 0
   const tl = gsap.timeline({
       default:{
           duration:1,
           ease:"power2.inOut"
       },
       scrollTrigger: {
           trigger:".page",
           start: "top top",
           end: "bottom bottom",
           scrub:.1,
       }
   })
    tl.to(normalCars.position, {x:1}, section)
    tl.to(raceCars.position, {x:-1}, section)
     //section 2
    section +=1
    tl.to(normalCars.position, {x:-3}, section)
    tl.to(normalCars.position, {z:-1}, section)
    tl.to(normalCars.rotation, {y:3}, section)
    tl.to(raceCars.position, {x:6}, section)
    //section 3
    section +=1
    tl.to(normalCars.position, {z:5, ease:"power4.out"}, section)
    tl.to(raceCars.position, {z:-1, x:1, ease:"power4.in"}, section)
    tl.to(raceCars.rotation, {y:3.5}, section)
     //section 4
    section +=1
    tl.to(normalCars.position, {x:-3,z:-2,y:-0.3}, section)
    tl.to(views[1], {bottom:1, ease:'none'},section)
    tl.to(normalCars.rotation, {y:3}, section)
};
const LoadingManager = new THREE.LoadingManager(()=>{
    setupAnimation()
})
const gltfLoader = new GLTFLoader(LoadingManager)
toLoad.forEach(item=>{
    gltfLoader.load(item.file, (model)=>{
        item.group.add(model.scene)
        scenes.real.add(item.group);
        Models[item.name] = item.group;
        const clone = item.group.clone()
        clones[item.name] = clone
        scenes.wire.add(clone);
    });
});